          <span>
              Ризики та можливі ускладнення в психологічному консультуванні
            </span>
          <p>
            Клієнту /тці важливо розуміти, що процес психологічного консультування буває дискомфортним і може викликати неприємні почуття: смуток, вину, сором, хвилювання, гнів, злість, розчарування, самотність і інші. Клієнт / ка може згадувати неприємні події під час терапії, після чого неприємні почуття можуть зберігатися кілька днів. Це нормально і зазвичай проходить при виконанні рекомендацій, отриманих від психолога. Іноді проблема може тимчасово погіршитися на початку психологічного консультування. Ця реакція також нормальна і логічна, коли людина намагається зробити серйозні життєві зміни.
            <br> Є також ризик того, що психолог не зможе допомогти клієнту /тці. Такі випадки обговорюються, і психолог може надати клієнту / ці допомогу в пошуку іншого фахівця.
