          </p>
          <p>
            Всі спроби встановити дружні, романтичні, публічні і інші відносини повинні припинятися. (Винятки повинні обговорюватися - наприклад: спільне відвідування громадських акцій, заходів, приналежність до однієї  і тієї ж спільноти, громади і т.д.. У подібних випадках психолог не ініціюватиме контакт). У разі якщо психолог зустрічає клієнта / ку поза свого кабінету, психолог не ініціює бесіди, а клієнт / ка може спокійно проігнорувати психолога і навіть не привітатися для гарантії конфіденційності.
          </p>
          <span>
              Конфіденційність інформації
            </span>
          <p>
            Вся інформація, отримана психологом від клієнта / ки, а також подробиці їхньої спільної роботи зберігаються в таємниці від третіх осіб (друзі, члени сім'ї, роботодавці та ін.) І організацій (податкова інспекція, поліція, і ін.). Психолог бере на себе зобов'язання захищати клієнта / ку від поширення конфіденційної інформації, за винятком випадків, коли існує загроза для безпеки клієнта, наприклад самогубство або безпеки інших осіб, наприклад, планування вбивства. У цих випадках психолог повинен зв'язатися з відповідними службами, і конфіденційність інформації може бути порушена.
            <br>
